import * as React from 'react';
import {ViewSubscription, Metadata} from "../../interfaces";
import * as $ from 'jquery';
const R = require('ramda');
import './subscription.less';
import subscriptionService from '../../services/subscriptions.service';
interface SubscriptionProps{
    subscription: ViewSubscription,
    metadata: Metadata,
    choosenTypes: string[]
}
interface SubscriptionState {
    subscription: ViewSubscription
}

class SubscriptionComponent extends React.PureComponent<SubscriptionProps, SubscriptionState> {
    constructor(props: SubscriptionProps){
        super(props);
        this.handleDelete = this.handleDelete.bind(this);
        this.handleValueChange = this.handleValueChange.bind(this);
        this.handleDeviationChange = this.handleDeviationChange.bind(this);
        this.typeChangeHandle = this.typeChangeHandle.bind(this);
        this.comparisonTypeChangeHandle = this.comparisonTypeChangeHandle.bind(this);
        this.handleChangeActivation = this.handleChangeActivation.bind(this);
        this.state = {
            subscription: null
        }
    }
    private nativeElements: {[key: string]: HTMLElement} = {};
    private $elements: {[key: string]: JQuery } = {};
    private _prevProps: any = {};

    private isInputActive(){
        const {subscription} = this.state;
        return !(subscription.process || subscription.active);
    }
    handleChangeActivation(event){
        let { subscription } = this.state;
        subscription.active = event.target.checked;
        subscription.process = true;
        subscription = Object.assign({}, this.state.subscription);
        subscriptionService.updateSubscription(subscription);
        this.setState({subscription});
    }
    handleDelete(){
        subscriptionService.removeSubscription(this.state.subscription);
    }
    initializeMaterial(){
        const self = this;
        this.$elements.subscriptionType = jQuery(this.nativeElements.subscriptionType).on('change', function(){
            self.typeChangeHandle($(this).val())
        });
        this.$elements.subscriptionComparison =  jQuery(this.nativeElements.subscriptionComparison).on('change', function(){
            self.comparisonTypeChangeHandle($(this).val())
        });
        this.$elements.subscriptionType.add(this.$elements.subscriptionComparison).material_select();
    }
    componentDidMount(){
       this.initializeMaterial();
    }
    componentDidUpdate(){
        this.$elements.subscriptionType.add(this.$elements.subscriptionComparison).material_select();

    }
    componentWillMount(){
        this.setState({subscription: R.clone(this.props.subscription)})
    }
    componentWillReceiveProps(nextProps){
        if(nextProps.subscription !== this._prevProps.subscription) {
            this._prevProps = nextProps;
            this.setState({subscription: R.clone(nextProps.subscription)})
        }

    }
    handleDeviationChange(event) {
        this.state.subscription.deviation = (event.target.validity.valid) ? event.target.value : this.state.subscription.deviation;
        this.setState({subscription: Object.assign({}, this.state.subscription)});
    }
    handleValueChange(event) {
        this.state.subscription.value = (event.target.validity.valid) ? event.target.value : this.state.subscription.value;
        this.setState({subscription: Object.assign({}, this.state.subscription)});
    }
    comparisonTypeChangeHandle(value) {
        const { subscription } = this.state;
        this.setState({subscription: Object.assign({}, subscription, {comparison: value})});
    }
    typeChangeHandle(value) {
        const { subscription } = this.state;
        this.setState({subscription: Object.assign({}, subscription, {type: value})});
    }
    render() {
        const { subscription} = this.state;
        const {metadata, choosenTypes} = this.props;
        return (
            <div className="card-panel white z-depth-1 subscription">
                <div className="subscription-header">
                    <div className="subscription-header__table">
                        <div className="switch subscription-header__cell">
                            <a className="btn btn-floating" onClick={this.handleDelete}>
                                <i className="material-icons  deep-purple-text text-lighten-5">delete</i>
                            </a>
                        </div>
                        <div className="switch subscription-header__cell">
                            <label className="right">
                                { subscription.process ? subscription.active ?
                                    'Activating' : 'Deactivating'
                                    : subscription.active ? 'Active': 'Inactive'
                                }
                                <input type="checkbox"
                                       checked={subscription.active}
                                       onChange={this.handleChangeActivation}/>
                                <span className="lever"></span>
                            </label>
                        </div>
                    </div>
                </div>
                <div className="subscription__body">
                    <div className="input-field subscription__input">
                        <select ref={(select)=> this.nativeElements.subscriptionType = select}
                                disabled={!this.isInputActive()}
                        className="subscription__select subscription__type">
                            <option value="" disabled selected>Choose type</option>
                            {metadata.subscriptionTypes.map((type, index)=>{
                                return <option value={type}
                                               disabled={choosenTypes.indexOf(type) > -1}
                                               key={index}
                                               selected={type === subscription.type}>
                                    {type}
                                    </option>;
                            })}
                        </select>
                        <label>Your subscription type</label>
                    </div>
                    <div className="input-field subscription__input">
                        <select
                            ref={(select)=> this.nativeElements.subscriptionComparison = select}
                            disabled={!this.isInputActive()} className="subscription__select subscription__comparison">
                            <option value="" disabled selected>Choose type</option>
                            {metadata.comparisonTypes.map((type, index)=>{
                                return <option
                                    value={type}
                                    key={index}
                                    selected={type === subscription.comparison}>
                                    {type}
                                    </option>;
                            })}
                        </select>
                        <label>Your subscription comparison type</label>
                    </div>
                    <div className="input-field subscription__input">
                        <input
                            disabled={!this.isInputActive()}
                            id="subscription_value"
                            type="text" pattern="[0-9]*"
                            value={subscription.value}
                            onInput={this.handleValueChange} />
                        <label
                            htmlFor="subscription_value"
                            className={subscription.value ? 'active': ''}>
                            Your subscription value
                        </label>
                    </div>
                    <div className="input-field subscription__input">
                        <input id="subscription_deviation"
                               type="text"
                               pattern="[0-9]*"
                               disabled={!this.isInputActive()}
                               value={subscription.deviation}
                               onInput={this.handleDeviationChange}/>
                        <label htmlFor="subscription_deviation"
                               className={subscription.deviation ? 'active': ''}>
                            Your subscription deviation
                        </label>
                    </div>


                </div>
            </div>
        );
    }

}

export default SubscriptionComponent;



