import * as React from 'react';
import SubscriptionComponent from "../subscription/subscription";
import {ViewSubscription, Metadata, Subscriber} from "../../interfaces";
import subscriptionsService from '../../services/subscriptions.service';
import './app.less';
import LoaderComponent from "../../components/loader";

interface AppState {
    subscriptions: ViewSubscription[],
    metadata: Metadata,
    loading: boolean,
    ready: boolean,
    subscriber: Subscriber,
    choosenSubscriptionTypes: string[]
}
class AppComponent extends React.PureComponent<any, AppState> {
    constructor(props: any){
        super(props);
        this.state = {
            subscriptions: null,
            metadata: null,
            loading: true,
            ready: false,
            subscriber: null,
            choosenSubscriptionTypes: []
        };
        this.handleAddClick = this.handleAddClick.bind(this);
    }
    private handleAddClick(){
        if(!subscriptionsService.isEmptySubscriber(this.state.subscriber)){
            if(!this.noAvailableSubscriptions()) {
                this._addSubscription();
            }
        } else {
            this._addSubscriber();
        }
    }
    private noAvailableSubscriptions(){
        return subscriptionsService.subscriptions.length
            >= subscriptionsService.metadata.subscriptionTypes.length;
    }
    private _addSubscription(){
        subscriptionsService.addSubscription();
    };
    private _addSubscriber(){
        this.setState({loading: true})
        subscriptionsService.addSubscriber().then(()=>{
            this.setState({loading: false})
        });
    };
    componentDidMount(){
        subscriptionsService.initialize();
        subscriptionsService.ready$.first().subscribe(()=>{
            subscriptionsService.subscriptions$.subscribe((subscriptions)=>{
                this.setState({subscriptions})
            });
            subscriptionsService.metadata$.subscribe((metadata)=>{
                this.setState({metadata})
            });
            subscriptionsService.subscriber$.subscribe((subscriber)=>{
                this.setState({subscriber})
            });

            subscriptionsService.subscriptions$.map((subscriptions)=>{
                return subscriptions.filter((subscription)=> {
                    return subscription.active;
                }).map((subscription)=>{
                    return subscription.type;
                });
            }).subscribe((subscriptionTypes)=>{
                this.setState({choosenSubscriptionTypes: subscriptionTypes})
            });
            this.setState({loading: false, ready: true});
        })

    }

    render() {
        const { subscriptions, metadata,choosenSubscriptionTypes, loading, ready} = this.state;

        return (
            <div className="layout  deep-purple lighten-5 ">
                <div className="layout__content deep-purple lighten-5 ">
                    {
                        metadata && subscriptions && (subscriptions || []).map((subscription)=> {
                            return <SubscriptionComponent
                                choosenTypes={choosenSubscriptionTypes}
                                subscription={subscription} key={subscription.uid || subscription.hash}
                                metadata={metadata}></SubscriptionComponent>
                        })}
                    <div className="center-align">
                        {loading && <LoaderComponent></LoaderComponent> }
                        { ready &&
                        <a  onClick={this.handleAddClick}
                            className={'btn btn-floating btn-large red accent-3  layout__add ' +
                            (this.noAvailableSubscriptions() || loading  ? 'disabled': 'pulse')} >
                            <i className="material-icons">add</i>
                        </a>
                        }
                    </div>
                </div>
            </div>

        );
    }

}


export default AppComponent;



