
import {hasOwnProperty} from "tslint/lib/utils";
export interface Subscription {
    uid?:string;
    type: string;
    value: number;
    comparison: string;
    lastValue?: number;
    deviation: number;
    active: boolean;
}
export interface Subscriber {
    token: string;
    ua: string;
    subscriptions: ViewSubscription[];
}
export interface ViewSubscription extends Subscription{
    process?: boolean;
    hash?: string;
}


export interface Metadata {
    comparisonTypes:string[];
    subscriptionTypes:string[];
}

export class SubscriptionModel implements Subscription{
    uid?: string;
    type: string;
    value: number;
    comparison: string;
    deviation: number;
    active: boolean;

    constructor(subscription: ViewSubscription){
        debugger
        if(subscription.uid) {
            this.uid = subscription.uid;
        }
        this.type = subscription.type;
        this.value = subscription.value;
        this.comparison  = subscription.comparison;
        this.deviation = subscription.deviation;
        this.active = subscription.active;
    }
}




