import { BehaviorSubject, Observable} from "rxjs";
import 'rxjs/add/operator/filter';
const R = require('ramda');
import {ViewSubscription, Metadata, Subscriber, Subscription} from "../interfaces";
import {subscribers} from "../constants";
class StorageService {
 private _metadata$ = new BehaviorSubject<Metadata>(null)
 private _subscriber$ = new BehaviorSubject<Subscriber>(null)
 public get subscriber(){
  return this._subscriber$.getValue();
 }
 public get metadata(){
  return this._metadata$.getValue();
 }
 public get subscriptions(){
  if(this.subscriber){
   return this.subscriber.subscriptions || []
  }
  return null;
 }
 public get subscriber$(): Observable<Subscriber>{
  return this._subscriber$.filter(x=>x !== null);
 }
 public get metadata$(): Observable<Metadata>{
  return this._metadata$.filter(x=>x !== null);
 }
 public get subscriptions$(): Observable<Subscription[]>{
  return this.subscriber$.map((subscriber)=> subscriber.subscriptions || []).filter(x=>x !== null);
 }

 public addSubscriber(subscriber: Subscriber){
  const _subscriber = R.clone(subscriber);
  this._subscriber$.next(_subscriber);
 }
 public addMetaData(metadata:  Metadata) {
  this._metadata$.next(metadata);
 }
 public updateSubscription(subscription: ViewSubscription){
  const  subscriptions = this.subscriptions.map((_subscription)=>{
   if(_subscription.hash  === subscription.hash) {
    return Object.assign({}, subscription);
   }
   return _subscription;
  });
  this._subscriber$.next(Object.assign({}, this.subscriber, {subscriptions}));

 }
 public updateSubscriber(subscriber: Subscriber){
  this._subscriber$.next(Object.assign({}, subscriber));
 }

 public removeSubscription(uid: string, hash?: string){
  const  subscriptions = this.subscriptions.filter(subscription=>{
   if(uid){
    return subscription.uid !== uid;
   }
   if(hash) {
    return subscription.hash !== hash;
   }

  });

  this._subscriber$.next(Object.assign({}, this.subscriber, {subscriptions}));
 }
 addSubscription(subscription){
  const subscriptions = [...this.subscriptions, subscription];
  this._subscriber$.next(Object.assign({}, this.subscriber, {subscriptions}));
 }
 public getSubscription(hash: string){
  return this.subscriptions.find((subscription)=>{
   return subscription.hash === hash;
  })
 }
 public get storageReady$() {
  return Observable.zip(
      this.metadata$,
      this.subscriptions$,

  ).take(1)
 }
}

export default new StorageService();


