import {applyTemplate} from "../constants";
const config = require('../config.json');
import * as $ from 'jquery';
import storageService from './storage.service'
import {Subscriber, SubscriptionModel, ViewSubscription} from "../interfaces";
import tokenService from './token.service';
const shortid = require('shortid');
const R = require('ramda');

class SubscriptionsService {
    private api = config.API;

    public initialize() {
        const storeToken = tokenService.getFromStoreToken();
        this.getMetadata();
        if(storeToken){
            this.getSubscriber(storeToken).then(()=>{
                this._initializeToken();
            });
        } else {
            storageService
                .addSubscriber(<Subscriber>{})
        }
    }
    private _initializeToken() {
        return tokenService.on('refreshToken', (newToken:string, oldToken: string)=>{
            this._refreshSubscriber(oldToken, newToken);
        }).initialize().getToken()
    }
    public metadata$ = storageService.metadata$;
    public subscriber$ = storageService.subscriber$;
    public subscriptions$ = storageService.subscriptions$;
    public get subscriber(){
        return storageService.subscriber;
    }
    public get metadata(){
        return storageService.metadata;
    }
    public get subscriptions(){
        return storageService.subscriptions;
    }
    public ready$ =  storageService.storageReady$;

    private _refreshSubscriber(oldToken, newToken){
        return $.ajax({
            url: applyTemplate(this.api.host + this.api.endpoints.subscriber.refresh, { token: oldToken }),
            type: "PUT",
            crossDomain: true,
            data: {
                newToken: newToken
            }
        }).then(()=>{
            storageService.updateSubscriber(Object.assign(this.subscriber, {token: newToken}))
        });
    }
    public getSubscriber(token){
        return $.ajax({
            url: applyTemplate(this.api.host + this.api.endpoints.subscriber.get, { token: token  }),
            type: "GET",
            crossDomain: true
        }).then((subscriber)=>{
            if(subscriber){
                storageService
                    .addSubscriber(Object.assign({}, subscriber,
                        {
                            subscriptions: subscriber.subscriptions.map((subscription)=> this._processSubscription(subscription))
                        }));
            } else {
                storageService
                    .addSubscriber(<Subscriber>{})
            }

        });
    }
    public addSubscription(){
        storageService.addSubscription(this._processSubscription({}));
    }
    public addSubscriber() {
       return this._initializeToken().then((token) => {
            return $.ajax({
                url: this.api.host + this.api.endpoints.subscriber.add,
                type: "POST",
                crossDomain: true,
                data: JSON.stringify({
                    subscriber: {
                        token: token,
                        ua: navigator.userAgent,
                        subscriptions: []
                    }
                }),
                contentType: 'application/json'
            }).then((subscriber) => {
                if (subscriber) {
                    storageService.addSubscriber(subscriber);
                    this.addSubscription();
                }
            })
        });

    }
    private _processSubscription(subscription): ViewSubscription{
        return Object.assign({}, subscription, {
            hash:shortid.generate()
        });
    }
    public updateSubscription(subscription: ViewSubscription){
        return $.ajax({
            url: applyTemplate(this.api.host + this.api.endpoints.subscriptions.update, {
                token: storageService.subscriber.token
            }),
            type: "PUT",
            crossDomain: true,
            data: {
                subscription: new SubscriptionModel(subscription)
            }
        }).then((_subscription)=>{
            storageService.updateSubscription(Object.assign({}, _subscription, {process: false, hash: subscription.hash}));
        }).catch(()=>{
            storageService.updateSubscription(Object.assign({}, subscription, {process: false, active: !subscription.active}));
        });
    }

    public removeSubscription(subscription: ViewSubscription){
        if(subscription.uid) {
            return $.ajax({
                url: applyTemplate(this.api.host + this.api.endpoints.subscriptions.delete, {
                    token: storageService.subscriber.token,
                    uid: subscription.uid
                }),
                type: "DELETE",
                crossDomain: true
            }).then(()=>{
                //storageService.updateSubscription(storageService.getSubscription(subscription.hash));
                storageService.removeSubscription(subscription.uid);
            });
        } else {
            storageService.removeSubscription(null, subscription.hash);
        }
    }

    public getMetadata() {
        return $.ajax({
            url: this.api.host + this.api.endpoints.metadata,
            type: "GET",
            crossDomain: true
        }).then((metaData)=>{
            storageService.addMetaData(metaData);
        });
    }

    public isEmptySubscriber(subscriber: any) {
        return R.equals(subscriber, {});
    }
}

export default new SubscriptionsService();


