declare let showNotification;
class TokenService {
    private messaging: any = null;
    private TOKEN_KEY: string =  '__token__';
    private listeners:any = {};

    initialize(){
        if (window.location.protocol === 'http:' &&
            'Notification' in window &&
            'serviceWorker' in navigator &&
            'localStorage' in window &&
            'fetch' in window &&
            'postMessage' in window
        ) {
            this.messaging = (window as any).firebase.messaging();
            // already granted
            if ((Notification as any).permission === 'granted') {
                this.getToken();
            }

            this.messaging.onMessage((payload)=> {
                navigator.serviceWorker.register('/serviceworker/messaging-sw.js');
                Notification.requestPermission((permission)=> {
                    if (permission === 'granted') {
                        navigator.serviceWorker.ready.then((registration)=>{
                            return showNotification(registration, payload);
                        }).catch((error)=> {
                            console.log('ServiceWorker registration failed.', error);
                        });
                    }
                });

            });
            // Callback fired if Instance ID token is updated.
            this.messaging.onTokenRefresh(()=> {
                this.messaging.getToken()
                    .then((refreshedToken) => {
                        console.log('Token refreshed.');
                        // Send Instance ID token to app server.
                        // sendTokenToServer(refreshedToken);
                        this._fireListeners('refreshToken', refreshedToken, this.getFromStoreToken());
                        this.storeToken(refreshedToken);
                    })
                    .catch(function (error) {
                        console.log('Unable to retrieve refreshed token.', error);
                    });
            });

        } else {
            console.warn('This browser does not support desktop notification.');
            console.log('Is HTTPS', window.location.protocol === 'https:');
            console.log('Support Notification', 'Notification' in window);
            console.log('Support ServiceWorker', 'serviceWorker' in navigator);
            console.log('Support LocalStorage', 'localStorage' in window);
            console.log('Support fetch', 'fetch' in window);
            console.log('Support postMessage', 'postMessage' in window);
        }
        return this;
    };
    storeToken(token) {
        window.localStorage.setItem(this.TOKEN_KEY, token);
    };

    getFromStoreToken() {
       return window.localStorage.getItem(this.TOKEN_KEY);
    };

    unstoreToken() {
        window.localStorage.removeItem(this.TOKEN_KEY);
    };
    getToken() {
        return this.messaging.requestPermission()
            .then(()=> {
                // Get Instance ID token. Initially this makes a network call, once retrieved
                // subsequent calls to getToken will return from cache.
                return this.messaging.getToken()
                    .then((currentToken)=>{
                        if (currentToken) {
                            this.storeToken(currentToken);
                            this._fireListeners('obtainToken', currentToken);
                            return currentToken;
                        } else {
                            console.log('No Instance ID token available. Request permission to generate one.');
                            return null;
                        }
                    })
                    .catch(function (error) {
                        console.log('An error occurred while retrieving token.', error);
                        //  setTokenSentToServer(false);
                    });
            })
            .catch(function (error) {
                console.log('Unable to get permission to notify.', error);
            });
    };
    deleteToken = function () {
        return this.messaging.getToken()
            .then((currentToken)=> {
                return this.messaging.deleteToken(currentToken)
                    .then(()=> {
                        console.log('Token deleted.');
                        this._fireListeners('deleteToken', currentToken);
                        this.unstoreToken();
                        //setTokenSentToServer(false);
                    })
                    .catch(function (error) {
                        console.log('Unable to delete token.', error);
                    });
            })
            .catch(function (error) {
                console.log('Error retrieving Instance ID token.', error);
            });

    };
     private _fireListeners(name,...args) {
        if (this.listeners[name]) {
            this.listeners[name].forEach((listener)=> {
                listener.apply(this, args);
            })
        }
    }

    on(name, listener) {
        this.listeners[name] = this.listeners[name] || [];
        this.listeners[name].push(listener);
        return this;
    }
}
export default new TokenService();



